.App {
  text-align: center;
  display: flex;
  background-color: #320063;
  color: white;
  position: absolute;
  overflow-y: scroll;
  top:0;
  left: 0;
  right: 0;
  bottom: 0;
}

.sidemenu{
  width: 240px;
  border: 1px solid white;
  padding: 10px;
  background-color: #180030;
  height: 100%;
}

.title-fsb{
  padding: 10px;
  height: 10px;
}

.side-menu-button{
  padding: 15px;
  border :1px solid white;
  border-radius: 5px;
  text-align: left;
  transition: ease 0.25s all;
}

.side-menu-button:hover{
  background-color: rgba(255, 255, 255,0.1);
}

.side-menu-button span{
  padding-left: 6px;
  padding-right: 12px;
}

.chatBox{
  flex: 1;
  position: relative;
  height: calc(100vh);
  overflow-y: auto;
}

.chat-input-holder{
  padding: 12px;
  position: sticky;
  bottom: 0;
  left: 0;
  right: 0;
}

.chat-input-textarea{
  background-color: #5d03b8;
  width: 95%;
  font-size: 1.25em;
  border-radius: 5px;
  border: none;
  margin: 12px;
  outline: none;
  padding: 10px;
  box-shadow: 0 8px 4px 0 rgba(0,0,0,0.5);
}

.chat-log{
  text-align: left;
}

.chat-message.chatbot{
  background-color: #5d03b8;
}

.chat-message-center{
  max-width: 480px;
  margin-left: 10px;
  margin-right: 10px;
  display: flex;
  padding: 12px;
  padding-left: 40px;
  padding-right: 40px;
}

.avatar{
  background-color: rgb(216, 255, 251);
  border-radius: 20%;
  width: 40px;
  height: 40px;
}

.avatar.chatbot{
  background-color: rgb(242, 242, 242);
  border-radius: 20%;
  width: 40px;
  height: 40px;
}

.message{
  padding-left: 40px;
  padding-right: 40px;
}

@media only screen and (max-width: 600px) {
  .App {
    flex-direction: column;
    align-items: stretch;
  }
  
  .sidemenu {
    width: 100%;
    height: auto;
  }
  
  .chatBox {
    height: calc(100vh - 160px);
  }
  
  .chat-input-textarea {
    width: 90%;
    margin: 6px;
  }
  
  .chat-message-center {
    padding: 10px;
    padding-left: 30px;
    padding-right: 30px;
  }
  
  .avatar {
    width: 30px;
    height: 30px;
  }
  
  .message {
    padding-left: 30px;
    padding-right: 30px;
  }
}
